.loginSection {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  background: linear-gradient(180deg, #ffffff 0%, #ffffff 25%, #bedbb0 92.19%);
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
}
 
.registerLink {
    margin-right: 14px;
    letter-spacing: -0.02em;
    color: rgba(255, 255, 255, 0.3);
    font-size: 18px;
    font-weight: 500;
    font-style: normal;
    line-height: 27px;   
}

.registerLink:hover,
.registerLink:focus {
  color: #bedbb0;
}

.loginLink {
    top: 40px;
    left: 160px;
    letter-spacing: -0.02em;
    color: var(--lightColor);
    font-size: 18px;
    font-weight: 500;
    font-style: normal;
    line-height: 27px;
}
 