.introContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  background: linear-gradient(180deg, #ffffff 0%, #ffffff 25%, #bedbb0 92.19%);
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
}

.introWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.logo {
  width: 124px;
  height: 124px;
  margin-bottom: 14px;
  background-image: url(../../images/first_m.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.logoWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 14px;
}

.introSvg {
  height: 40px;
  width: 40px;
}

.title {
  font-family: 'Poppins';
  font-size: 28px;
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: -1.12px;
  color: #161616;
}

.secondText {
  width: 335px;
  color: #161616;
  margin-top: 25px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
  letter-spacing: -0.28px;
  font-family: 'Poppins';
}

.authWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 48px;
}

.authWrapper.btnLogin:hover + .btnRegister {
  background: transparent;
  color: white;
}

.buttonLinkLogin {
  text-transform: none;
  text-decoration: none;
  font-weight: 500;
  line-height: 21px;
  color: rgba(22, 22, 22, 1);
  font-size: 14px;
}

.btnRegister {
  color: white;
  background-color: rgba(22, 22, 22, 1);
}

.buttonLinkRegister {
  text-transform: none;
  text-decoration: none;
  font-weight: 500;
  line-height: 21px;
  color: white;
  font-size: 14px;
}

.buttonLink:hover {
  color: white;
}

@media screen and (min-width: 768px) {
  .logo {
    width: 162px;
    height: 162px;
    margin-bottom: 24px;
    background-image: url(../../images/first_dt.png);
  }
  .svg {
    height: 48px;
    width: 48px;
  }
  .title {
    font-size: 40px;
    letter-spacing: -1.6px;
  }
  .secondText {
    width: 473px;
  }
}
