.loginSection {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background: linear-gradient(180deg, #ffffff 0%, #ffffff 25%, #bedbb0 92.19%);
    background-repeat: no-repeat;
    background-size: cover;
    flex-direction: column;
  }
   
  .loginLink {
      margin-left: 14px;
      letter-spacing: -0.02em;
      color: rgba(255, 255, 255, 0.3);
      font-size: 18px;
      font-weight: 500;
      font-style: normal;
      line-height: 27px;   
  }
  
  .loginLink:hover,
  .loginLink:focus {
    color: #bedbb0;
  }
  
  .registerLink {
      top: 40px;
      left: 160px;
      letter-spacing: -0.02em;
      color: var(--lightColor);
      font-size: 18px;
      font-weight: 500;
      font-style: normal;
      line-height: 27px;
  }
  
  
   